// вот вам функция для сохранения объектов в память браузера
// (данные в этом хранилище сохраняться даже при перезагрузке компа):
export type ValueType = string;

export function saveState<ValueType>(key: string, state: ValueType) {
    const stateAsString = JSON.stringify(state);
    localStorage.setItem(key, stateAsString);
}

// и вот вам функция для получения сохранённого объекта в памяти браузера:
export function restoreState<ValueType>(key: string, defaultState: ValueType) {
    let state = defaultState
    const stateAsString = localStorage.getItem(key)
    if (stateAsString !== null) state = JSON.parse(stateAsString) as ValueType
    return state
}

